import React from 'react';
import cl from './style/MessageInfo.module.css';
import Burger from './Burger';
import {
  selectPopUp,
  trueCheckValidation,
  falseCheckValidation
} from '../../redux/slices/popUpSlice';
import { useSelector, useDispatch } from 'react-redux';
import MessageInfoButton from './UI_MessageInfo/MessageInfoButton';

const MessageInfo: React.FC = () => {
  const { checkValidation } = useSelector(selectPopUp)
  // console.log(checkValidation)
  // const dispatch = useDispatch()

  return (
    <div className={cl.container}>
      <div className={cl.innerContainer}>
        <div className={cl.burger}>
          <Burger />
        </div>
        <p className={cl.text}>             <br />   <span> </span></p>
        <div className={cl.buttons}>

          <MessageInfoButton
            callback={falseCheckValidation}
            value={!checkValidation}
          >
             
          </MessageInfoButton>

          <MessageInfoButton
            callback={trueCheckValidation}
            value={checkValidation}
          >
            
          </MessageInfoButton>

        </div>
      </div>
    </div>

  );
}
export default MessageInfo;