import React from 'react';
import cl from "./PopUpMessageOption.module.css"
import { useSelector, useDispatch } from 'react-redux';
import { changePopUpToFalse as changeFalse, selectPopUp } from '../../../redux/slices/popUpSlice';
import PizzaOptions from './pizzaElement/PizzaOptions';
import PizzaPrice from './pizzaElement/PizzaPrice';
import useResponsePizzaDate from '../../business/customHooks/utilits';

type Pizza = {
  pizza: {
    imageUrl: string;
    id: string;
    title: string;
    types: number[];
    price: number[];
    sizes: number[];
  } 
}
const PopUpMessageOption:React.FC = () => {
  const dispatch = useDispatch();
  const { statePopUpMessage: view } = useSelector(selectPopUp)
  const { pizza }: Pizza = useSelector(selectPopUp)
  const { title } = { ...pizza }

  const { paramOptions, priceOptions } =  useResponsePizzaDate({ ...pizza })
  
  const condition = [cl.background]
  if (view) {
        condition.push(cl.active)
  }

    return (
      <div
        className={condition.join(" ")}
        onClick={() => dispatch(changeFalse())}
      >
        <div
          onClick={(event) => event.stopPropagation()}
          className={cl.message}
        >
          {view && paramOptions && priceOptions &&
            <div className={cl.wrapper}>
              <div className={cl.title}> {title} </div>
              <div className='options'>
                <PizzaOptions {...paramOptions } />
              </div>
              <PizzaPrice {...priceOptions} />
            </div> 
          }
        </div>
      </div>
    );
}
export default PopUpMessageOption;