import React from 'react';
import PizzaOptions from './pizzaElement/PizzaOptions';
import PizzaPrice from './pizzaElement/PizzaPrice';
import PizzaBurgerOption from './pizzaElement/PizzaBurgerOption';
import useResponsePizzaDate from '../../business/customHooks/utilits';

type PizzaProp = {
  pizza:
  {
    id: string;
    category: number;
    imageUrl: string;
    price: number[];
    rating: number;
    sizes: number[];
    title: string;
    types: number[];
  }
}

const PizzaElement: React.FC<PizzaProp> = ({ pizza }) => {

  const {imageUrl,title} = {...pizza}
  const {paramOptions, priceOptions } = useResponsePizzaDate({...pizza})

  return (
    <div className="pizzas__pizza pizza">
      <div className="pizza__img">
        <img className="_ibg" src={imageUrl} alt="pizza" />
      </div>
      <div className="pizza__title">{title}</div>
      <PizzaBurgerOption pizza={pizza} /> {/* this element shows only when size of Display less than 460px, css settings changes vision */} 
      <div className="pizza__options options" >
        <PizzaOptions {...paramOptions} />
      </div>
      <div className="pizza__body-price">
        <PizzaPrice {...priceOptions} />
      </div>
    </div>
  );
}
export default PizzaElement;