import React, { useEffect, useState } from "react";
import SortElement from "../components/main/pizza_page/SortElement";
import PizzasElement from "../components/main/pizza_page/PizzasElement";
import { useSelector, useDispatch } from "react-redux";
import { useAppDispatch } from "../redux/store";
import { fetchPizzas,selectCart} from "../redux/slices/cartSlice";
import {selectCategory, selecSort} from '../redux/slices/filterSlice'
import ErrorLoadPage from "./ErrorLoadPage";


const PizzasPage = () => {
  const indexCategory = useSelector(selectCategory);
  const sortPizza: string = useSelector(selecSort);
  const dispatch = useAppDispatch()

  const {status, error} = useSelector(selectCart)

  useEffect(() => {
    //@ts-ignore
    dispatch(fetchPizzas(indexCategory, sortPizza))
  }, [indexCategory, sortPizza]);


  return (
    <div className="main wall-paper">
      <div className="container">
        {error ?
         <ErrorLoadPage />
          :
          <>
            <SortElement />
            <PizzasElement />
          </>
        }
      </div>
    </div>
  );
}
export default PizzasPage;
