import React,{ useState, useRef, useEffect} from 'react';
import cl from './styles/TotalPage.module.css'
import { useSelector, useDispatch } from 'react-redux';
import { selectCart } from "../redux/slices/cartSlice";
import { Link } from 'react-router-dom';
import { selectCustomer } from '../redux/slices/customerSlice';
import Confetti from '../components/confety/confetti';

// import { refreshCart } from "../redux/slices/cartSlice";
// import { refreshFilter } from "../redux/slices/filterSlice";
// import { refreshPopUp } from "../redux/slices/popUpSlice";
// import { refreshCastomer } from "../redux/slices/customerSlice";





const TotalPage: React.FC = () => {
  const dispatch = useDispatch()
  // const refresh = () => {
  //   dispatch(refreshCart())
  //   dispatch(refreshFilter())
  //   dispatch(refreshPopUp())
  //   dispatch(refreshCastomer())
  // }

  const [isVisible, setIsVisible] = useState(false);
  const href = "http://localhost:3000/#"

  const { customerName, customerPhone, customerEmail, customerAdress } = useSelector(selectCustomer)
  const { deliveryIndex } = useSelector(selectCustomer)
  const delivery = (deliveryIndex == 0)? 350 : 0

  const { pizzas, price } = useSelector(selectCart)
  const nds = Math.floor(price / 100 * 20) 
  const month = ["", "", "", "", "", "", "", "", "", "", "", ""]
  function getZero(str: string | number): string {
    if (String(str).length < 2) return `0${str}`
    return `${str}`
  }       
  const dateTime = [
    getZero(new Date().getFullYear()),
    month[new Date().getMonth()],
    getZero(new Date().getDate()),
    "/",
    getZero(new Date().getHours()),
    ":",
    getZero(new Date().getMinutes()),
    ":",
    getZero(new Date().getSeconds()),
  ].join(" ")
  


  
  return ( 
    <div className="main wall-paper">
      <div className="container">
        <div className={cl.receiptPage}>
          <div className={cl.receipt}> 
            <div className={cl.receipt__title}>      </div>
          
            <div className={cl.receipt__subTitle}>    .  . </div>

            {/* <h1 style={{ fontSize: "30px" }}>{customerName}</h1> */}
            
            <div className={cl.receipt__body}>
              <div className={cl.receipt__paragraph}>
                <div className={cl.receipt__row}>
                  <div className={cl.receipt__right}>{dateTime}</div>
                  <div className={cl.receipt__left}> :  React</div>
                </div>
                <div className={cl.receipt__row}>
                  <div className={cl.receipt__right}></div>
                  <div className={cl.receipt__left}> 18</div>
                </div>
              </div>

              <div className={cl.receipt__paragraph}>
                {pizzas.map((pizza, index: number) => (
                  <ReceiptComponent key={index} {...pizza} />
                ))}
              </div>

              <div className={cl.receipt__subTitle}>  </div>

              <div className={cl.receipt__paragraph}>
                <div className={cl.receipt__row}>
                  <div className={cl.receipt__right}>  </div>
                  <div className={cl.receipt__left}>{price - nds} </div>
                </div>
                <div className={cl.receipt__row}>
                  <div className={cl.receipt__right}> 20%</div>
                  <div className={cl.receipt__left}>{nds} </div>
                </div>
                {(deliveryIndex == 0) ?
                  <div className={cl.receipt__row}>
                    <div className={cl.receipt__right}></div>
                    <div className={cl.receipt__left}>{delivery} </div>
                  </div>
                  :
                  null
              }
                <div className={cl.receipt__row}>
                  <div className={cl.receipt__right}>  </div>
                  <div className={cl.receipt__left}>{price + delivery} </div>
                </div>
              </div>

              <div className={cl.receipt__subTitle}>  </div>
              <div className={cl.receipt__paragraph}>
                <div> {customerName }</div>
                <div> {customerPhone }</div>
                <div> {customerEmail }</div>
                <div> {customerAdress }</div>
              </div>

              <div className={cl.receipt__paragraph}>
                <div className={cl.receipt__row}>
                  <div className={cl.receipt__right}> 000000001010172</div>
                  <div className={cl.receipt__left}> 99999732223232</div>
                </div>
                <div className={cl.receipt__row}>
                  <div className={cl.receipt__right}> 70000493333</div>
                  <div className={cl.receipt__left}> 97</div>
                </div>
                <div className={cl.receipt__row}>
                  <div className={cl.receipt__right}> </div>
                </div>
                <div className={cl.receipt__row}>
                  <div className={cl.receipt__right}> </div>
                </div>
                <div className={cl.receipt__row}>
                  <div className={cl.receipt__right}> </div>
                </div>

              </div>
            </div>
          </div>
          <Link
            to=""
            onClick={() => {
              if (!isVisible) setTimeout(() => {
                window.location.href = href
              }, 2000)
              setIsVisible(true)
              // refresh()
            }}
            className={cl.button}
            >
              
          </Link>        
          {isVisible && <Confetti/>}
        </div>
      </div>
    </div>

  );
}


interface ReceiptComponentProps {
  count: number;
  price: number;
  title: string;
}
const ReceiptComponent: React.FC<ReceiptComponentProps> = ({ count, price, title }) => {
  const wholePrice = count * price
  return (
    <div className={cl.receipt__row}>
      <div className={cl.receipt__productName}>{title}</div>
      <div className={cl.receipt__countPrice}>
        <div className={cl.receipt__count}>{count} </div>
        <div className={cl.receipt__price}>{wholePrice} </div>
      </div>
    </div>
  )
}


export default TotalPage;