import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { addPizza } from "../../../redux/slices/cartSlice";

type PizzaProp = {
  imageUrl: string;
  id: string;
  title: string;
  types: number[];
  price: number[];
  sizes: number[];
}

export default function useResponsePizzaDate({ imageUrl, id, title, types, sizes, price }:PizzaProp) {
  const dispatch = useDispatch()
  const [activType, setActiveType] = useState<number>(0)
  const [activSize, setActiveSise] = useState<number>(0)
  const paramOptions = {
    types,
    sizes,
    activType,
    activSize,
    setActiveType,
    setActiveSise,
  }
  const priceOptions = {
    andNewPizza,
    id,
    price,
    activSize,
  }
  function andNewPizza() {
    console.log("sizes[activSize]",sizes[activSize])
    const newPizza = {
      id: id, // type = number
      imageUrl: imageUrl, // type = string
      title: title, // type = string
      size: sizes[activSize], // type = number
      type: types[activType], // type = number
      price: price[activSize], // type = number
      count: 1, // type = number
    }
    dispatch(addPizza(newPizza))
  }
  return{paramOptions,priceOptions}
  }