import React from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';

function ButtomPayPizza() {
  const pizzas = useSelector((state) => Boolean(state.cart.pizzas.length))
  return (
    <>
    { pizzas &&
      <Link to="/order" className="bottom__button">
        <p> </p>
      </Link>}
    </>
    

    );
}
export default ButtomPayPizza;