import React, { useReducer, useState } from 'react';
import { useValidate } from './useValidate';
import { useSelector, useDispatch } from 'react-redux';
import { getCustomerDate } from '../../../redux/slices/customerSlice';


type ChangeType = React.ChangeEventHandler<HTMLInputElement>
type CangeBlur = React.FocusEventHandler<HTMLInputElement>
type PropsType = "phone" | "name" | "adress" | "email"

const useInput = (prop: PropsType) => {
  const dispatch = useDispatch()
  const [value, setValue] = useState("")
  const [isDirty, setDirty] = useState(false)
  const { error, errorStatus } = useValidate(prop, value)
  

  const onblur: CangeBlur = () => {
    setDirty(true)
  }
  const onchange: ChangeType = (e) => {
    dispatch(getCustomerDate({"type" :prop, "value": e.target.value}))
    setValue(e.target.value)
    
  }
  const errorVisible = (errorStatus && isDirty)


  return{value, onchange, onblur, error, errorVisible, errorStatus}
}
export default useInput;