import React, { useState } from 'react';
import PizzaElement from "./PizzaElement";
import PopUpMessageOption from './PopUpMessageOption';
import { useSelector} from 'react-redux';
import { selectPizzas } from "../../../redux/slices/cartSlice"


type Pizza = {
  category: number;
  id: string;
  imageUrl: string;
  price: number[];
  rating: number;
  sizes: number[];
  title: string;
  types: number[];
}[] | null

const PizzasElement: React.FC = () => {

  const pizzas: Pizza = useSelector(selectPizzas)

  
    return (
      <div className="pizzas">
        <PopUpMessageOption /> {/* this  element is activated by PizzaBurgerOptions via css stiles in PopUpMessageOption*/}
        <h2 className="pizzas__title"> </h2>
        <div className="pizzas__list">
          {(pizzas) ?
            pizzas.map((pizza, index: number) => {
              return <PizzaElement key={index} pizza={pizza} />
            })
            :
            null
          }
          
        </div>
      </div>
    );
}
export default PizzasElement;