import React, { useEffect, useState, useRef } from "react";
import { useSelector} from "react-redux";
import blackTriangle from "../../../../assets/img/black-triangle.svg"
import SortPopUp from "./SortPopUp";
import { selectFilter } from '../../../../redux/slices/filterSlice'

const SortType: React.FC = () => {
  const [window, setWindow] = useState<boolean>(false)
  const { sort } = useSelector(selectFilter)
  const sortRef = useRef<HTMLDivElement>(null)
  const popUpRef = useRef<HTMLDivElement>(null)


  function hideSortMenu(event: MouseEvent | null = null ) : void {
    if (!event) return
    if (!sortRef.current || !popUpRef.current) return
    
      const arr = event.composedPath();
      if (!window) {
        const result = arr.includes(sortRef.current);
        if (result) setWindow(true)
      } else {
        const result = arr.includes(popUpRef.current);
        if (!result) setWindow(false)
      }
    }

  useEffect(() => {
    hideSortMenu()
    document.body.addEventListener("click", hideSortMenu);
    return () => {
      document.body.removeEventListener("click", hideSortMenu);
    }
  },[window])


    return (
      <div className="sort__type" >
        <div className="type" ref={sortRef}>
          <div className="type__img">
            <img className="_ibg" src={blackTriangle} alt="" />
          </div>
          <div className="type__name"> :</div>
          <div className="type__choice">{sort[0]}</div>
          <div className="type__popUp" ref={popUpRef}>
            <SortPopUp window={window} />
          </div>
        </div>
      </div>
    );
}
export default SortType;