import React from 'react';

type DataProps = {
  li_s: number[] | string[];
  text: string | null;
  value: number| string;
  callback: (index: number) => void;
}

const LiComponents: React.FC<DataProps> = ({ li_s, text, value, callback }) => {
  return (
    <>
      {li_s.map((li, index: number) => (
        <li
          className={value == index ? "active" : ""}
          key={index}
          onClick={() => callback(index)}>
          {text} {li}
        </li>
      ))}
    </>
  );
}
export default LiComponents;