import React from 'react';
import { useDispatch } from 'react-redux';
import { changePopUpToTrue as changeTrue, getNewPizza } from '../../../../redux/slices/popUpSlice';

type PizzaProp = {
  pizza:
  {
    id: string;
    category: number;
    imageUrl: string;
    price: number[];
    rating: number;
    sizes: number[];
    title: string;
    types: number[];
  }
}
const PizzaBurgerOption: React.FC<PizzaProp> = ({ pizza }) => {
  const dispatch = useDispatch()
  function showPopUpMessage() {
    dispatch(getNewPizza(pizza))
    dispatch(changeTrue())
  }

  return (
    <>
      <div
        className="pizza__burger-options burger-options"
        onClick={showPopUpMessage}
      >
        <div className="burger-options" >
          <div>  </div>
        </div>
      </div>
    </>
  );
}
export default PizzaBurgerOption;