import React from 'react';
import cl from "./style/ModalWindow.module.css"
import { selectPopUp } from "../../redux/slices/popUpSlice"
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { hideMessage } from '../../redux/slices/popUpSlice'

interface IModal {
  children: React.ReactNode,
}
const ModalWindow: React.FC<IModal> = ({ children }) => {
  const { visible } = useSelector(selectPopUp)
  const dispatch = useDispatch()
  return (
    <>
        {
          visible &&
          <div className={cl.container}
            onClick={() => {  dispatch(hideMessage()) }}  
          >
            <div className={cl.message}
              onClick={(event) => event.stopPropagation()}
            >
              {children}
            </div>
          </div>
        }
    </>
  );
}
export default ModalWindow;