import React,{useState} from 'react';
import LiComponents from '../components/common/LiComponents';
import ModalWindow from '../components/common/ModalWindow';
import FormOrder from '../components/main/order_page/FormOrder';
import FormOrderOwn from '../components/main/order_page/FormOrderOwn';
import { useDispatch, useSelector } from 'react-redux';
import { selectCustomer, setDeliveryIndex } from '../redux/slices/customerSlice'
import { showOwnMessage, showInfoMessage } from "../redux/slices/popUpSlice"
import MessageInfo from '../components/common/MessageInfo';
// @ts-ignore
import { CSSTransition } from 'react-transition-group';
import { selectPopUp } from "../redux/slices/popUpSlice"


function OrderPage() {
  const { infoMessage } = useSelector(selectPopUp)
  const { deliveryIndex, deliveryList } = useSelector(selectCustomer)
  const view = infoMessage.currentCall
  const dispatch = useDispatch()

  function onclick(index: number) {
    dispatch(showOwnMessage(index))
    dispatch(setDeliveryIndex(index))
    dispatch(showInfoMessage())
  }

  return (
    <div className="main wall-paper">
      <div className="container">
        <div className='order'>
          <h3> </h3>
          <ul className="order__list">
            <LiComponents li_s={deliveryList} text={null} value={deliveryIndex} callback={onclick}/>
          </ul>

          
          <CSSTransition
            timeout={1300}
            classNames="pop-up__message"
            in={view}
            unmountOnExit={true}
            mountOnEnter={true}
          >
            <MessageInfo />
          </CSSTransition>
          
          <ModalWindow >
            <FormOrder />
            <FormOrderOwn />
          </ModalWindow>

        </div>
      </div>
    </div>
    
  );
}
export default OrderPage;