import { createSlice } from "@reduxjs/toolkit";
import { RootState } from "../store";


interface Iinitial {
  customerName: string;
  customerPhone: string;
  customerEmail: string;
  customerAdress: string;
  deliveryIndex: string | number;
  deliveryList: string[];
}

const initialState:Iinitial = {
  customerName: "",
  customerPhone: "",
  customerEmail: "",
  customerAdress: "",
  deliveryIndex: "",
  deliveryList: [" ", ""],
}

const customerSlice = createSlice({
  name: "customer",
  initialState,
  reducers: {
    getCustomerDate(state, action) {
      // console.log(state.customerName)
      switch (action.payload.type) {
        case "phone":
          state.customerPhone = action.payload.value
          break
        case "name":
          state.customerName = action.payload.value
          break
        case "adress":
          state.customerAdress = action.payload.value
          break
        case "email":
          state.customerEmail = action.payload.value
          break
      }
    },
    setDeliveryIndex(state, action) {
      state.deliveryIndex = action.payload
    },
    refreshCastomer(state) {
      state = {
      customerName: "",
      customerPhone: "",
      customerEmail: "",
      customerAdress: "",
      deliveryIndex: "",
      deliveryList: [" ", ""],
      }
    }

  }
})

export default customerSlice.reducer
export const selectCustomer = (state:RootState) => state.customer
export const {
  getCustomerDate,
  setDeliveryIndex,
  refreshCastomer,
} = customerSlice.actions
