import React from 'react';
// @ts-ignore
import { ReactComponent as Plus_button} from "../../../assets/img/plus_1.svg"; 
import { useDispatch } from 'react-redux';
import {  addOne } from '../../../redux/slices/cartSlice';

interface AddOnePizzaProps{
  pizza: {
    id: string;
    imageUrl: string;
    title: string;
    size: number;
    type: number;
    price: number;
    count: number
  }
}

const AddOnePizza: React.FC<AddOnePizzaProps> = ({ pizza }) =>{
  const dispatch = useDispatch()
  return (
    <button
      className="pizza-cart__minus"
      onClick={() => {
        dispatch(addOne(pizza))
      }}>
      <Plus_button className="svg_button" />
    </button>
  );
}
export default AddOnePizza;