import React from 'react';
import { changeSort, selectFilter } from "../../../../redux/slices/filterSlice";
import { useSelector, useDispatch } from "react-redux";
import LiComponents from "../../../common/LiComponents";

const SortPopUp: React.FC <{window: boolean}> = ({ window }) => {
  const dispatch = useDispatch();
  const { listSort, sort } = useSelector(selectFilter);
  function changeSortFn(index: number) {
    dispatch(changeSort(index));
  }
  return (
    <>
      {window && (
        <ul className="popUp">
          <LiComponents
            li_s={listSort}
            text={null}
            value={sort[2]}
            callback={changeSortFn}
          />
        </ul>
      )}
    </>
  );
}
export default SortPopUp