import React from 'react';
import ButtonClearAllPizza from '../../common/ButtonClearAllPizza';
import { ReactComponent as ShoppingCart } from "../../../assets/img/shopping-cart.svg";
import CartElements from '../../../components/main/cart_page/CartElements';


function CartPizzas() {
  return (
    <>
      <div className="cart-page__top top">
        <div className="top__basket" >
          <div className="top__basket-img">
            <ShoppingCart />
          </div>
          <h2></h2>
        </div>
        <ButtonClearAllPizza />
      </div>
      <CartElements />      
    </>
  );
}
export default CartPizzas;