import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { RootState } from "../store";

interface InitialFilter{
  category: [string, string | number];
  sort: [string,string,string|number];
  listCategories: string[];
  listSort: string[];
  listSort2: string[];
  closeMenu: boolean;
}

const initialState:InitialFilter = {
  category: ["", ""],
  sort: ["", "", ""],
  listCategories: [
    "",
    "",
    "",
    "",
    "",
    "",
  ],
  listSort: ["", "", ""],
  listSort2: ["rating", "title", "price"],
  closeMenu: false,
};

const filterSlice = createSlice({
  name: "filter",
  initialState,
  reducers: {
    changeCaterory(state, action: PayloadAction<number>) {
      if (action.payload === 0) {
        state.category[1] = ""
      } else {
        state.category[1] = action.payload;
      }
      state.category[0] = state.listCategories[action.payload];
      state.closeMenu = !state.closeMenu // closes all open menu
    },
    changeSort(state, action: PayloadAction<number>) {
      state.sort[0] = state.listSort[action.payload];
      state.sort[1] = state.listSort2[action.payload];
      state.sort[2] = action.payload;
      state.closeMenu = !state.closeMenu; // closes all open menu
    },
    refreshFilter(state) {
      state = {
      category: ["", ""],
      sort: ["", "", ""],
      listCategories: [
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      listSort: ["", "", ""],
      listSort2: ["rating", "title", "price"],
      closeMenu: false,
      };
    }
  }
})


export default filterSlice.reducer;
export const selectCategory = (state: RootState) => state.filter.category[1]
export const selecSort = (state: RootState) => state.filter.sort[1]
export const selectFilter = (state: RootState) => state.filter
export const selectCathegory = (state: RootState): string | number  => {
    const index = state.filter.category[1]
    return index === "" ? 0 : index
}
export const {
  changeCaterory,
  changeSort,
  refreshFilter
} = filterSlice.actions;