import React from 'react';
import { useSelector, useDispatch } from "react-redux";
import { changeCaterory } from "../../../../redux/slices/filterSlice"
import LiComponents from '../../../common/LiComponents';
import { selectFilter, selectCathegory } from '../../../../redux/slices/filterSlice'

const Categories: React.FC = () => {
  const { listCategories } : { listCategories: string[] } = useSelector(selectFilter)
  const dispatch = useDispatch();
  const category = useSelector(selectCathegory)

  function changeCateroryFn(index: number) {
    dispatch(changeCaterory(index))
  }
  
    return (
      <div className="sort__categories">
        <ul className="categories">
          <LiComponents
            li_s={listCategories}
            text={null}
            value={category}
            callback={changeCateroryFn}
          />
        </ul>
      </div>
    );
}
export default Categories;