import React,{useEffect} from 'react';
import cl from './style/Input.module.css'
// @ts-ignore
import { CSSTransition } from 'react-transition-group';
import { useSelector, useDispatch } from 'react-redux';
import { selectPopUp } from '../../../redux/slices/popUpSlice'
import { getCustomerDate } from '../../../redux/slices/customerSlice';

interface Idata {
  value: string;
  errorVisible: boolean;
  error: string;
  onchange: React.ChangeEventHandler<HTMLInputElement>;
  onblur: React.FocusEventHandler<HTMLInputElement>;
}
interface Iprops {
  type: string;
  placeholder: string;
  name: string;
  data: Idata;
}
interface Ierror {
  errorVisible: boolean;
  error: string
}


const Input: React.FC<Iprops> = ({
    type,
    placeholder,
    name,
    data,
    
}) => {

  const { checkValidation } = useSelector(selectPopUp)

  return (
    <div className={cl.container}>
      {
        checkValidation && 
        <Error errorVisible={data.errorVisible} error={data.error} />
      }
 
      {
        checkValidation ?
          <input className={cl.input}
            type={type}
            placeholder={placeholder}
            value={data.value}
            onChange={data.onchange}
            onBlur={data.onblur}
          />
          :
          <input
            type="text"
            className={cl.input}
            placeholder={placeholder}
            value={data.value}
            onChange={data.onchange}
        />
      }
      
    </div>
  );
}


const Error: React.FC<Ierror> = ({ errorVisible, error }) => {
  return (
    <CSSTransition
      timeout={400}
      classNames="messageError"
      in={errorVisible}
      unmountOnExit={true}
      mountOnEnter={true}
    >
      <div className={cl.messageError}>
        <p>{error}</p>
      </div>
    </CSSTransition>
  )
}


export default Input;