import React from 'react';
import cl from "./styles/MessageInfoButton.module.css"
import { useDispatch } from 'react-redux';

interface Iprops {
  children: string;
  callback: any;
  value: boolean;
}

function MessageInfoButton({ children, callback, value }: Iprops) {
  const dispatch = useDispatch()
  return (
    <button
      onClick={() => dispatch(callback())}
      className={value ? [cl.button, cl.active].join(" ") : cl.button}
    >
      {children}
    </button>
  );
}
export default MessageInfoButton;