import React from 'react';
import cl from "./style/Button.module.css"
import { useDispatch } from 'react-redux';
import { hideMessage, hideInfoMessage } from '../../../redux/slices/popUpSlice'
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { selectPopUp } from '../../../redux/slices/popUpSlice';


interface Iprops {
  list: boolean[]
}
type EventButton = React.MouseEventHandler<HTMLButtonElement>

const Button: React.FC<Iprops> = ({ list }) => {
  const { checkValidation } = useSelector(selectPopUp)

  const dispatch = useDispatch()

  // const result = 
  const result = checkValidation ?
    list.every(item => item === false)
    : true

  const submit: EventButton = (e) => {
    e.preventDefault()
    if (result) {
      dispatch(hideMessage()) // it hides order message
      dispatch(hideInfoMessage()) // it hides aditional check message      
    }
  }
  return (

      <button
        className={(result) ? cl.buttonAct : cl.button}
        onClick={submit}
    >
      {
        result ?
          <Link style={{ width: "100%", height: "100%", display: "block", borderRadius: "10px" }} to={"/total"}>
            
          </Link>
          :
          ""
      }

      </button>

      
    
  );
}
export default Button;