import React from 'react';
import ButtonBackPizza from '../components/common/ButtonBackPizza';
import { useSelector } from 'react-redux';
import CartPizzas from '../components/main/cart_page/CartPizzas';
import CartEmpty from '../components/main/cart_page/CartEmpty';
import ButtomPayPizza from '../components/common/ButtomPayPizza';



function CartPage() {
  const pizzas = useSelector((state) => Boolean(state.cart.pizzas.length))
  console.log("cart page",pizzas)

    return (
      <div className="main wall-paper">
        <div className="container">
          <div className="cart-page">
            {pizzas?<CartPizzas/> : <CartEmpty/>}
            <div className="cart-page__bottom bottom">
              <ButtonBackPizza/>
              <ButtomPayPizza/>
            </div>
          </div>
        </div>
      </div>
    );
}
export default CartPage;