import React,{useState, useEffect} from "react"

type InputType = "phone" | "name" | "password" | "email" | "adress"
export const useValidate = (prop: InputType, value: string): { error: string, errorStatus: boolean  } => {

  const [error, setError] = useState<string>("")
  const [errorStatus, setErrorStatus] = useState<boolean>(true)

  useEffect(() => {
    const number: number = value.length

    switch (prop) {
      case "email":
        const reger = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        if (value.length < 1) {
          setError("email    ")
          setErrorStatus(true)
        }
        else if (!reger.test(value)){
          setError("  email")
          setErrorStatus(true)
        }
        else setErrorStatus(false)
        break

      case "password":
        
        if (number < 1) setError("password won't to be an empty")
        else if (number < 5) setError("your password is very short")
        else if (number > 11) setError("your password is very long")
        else if (value.includes(" ")) setError("your password has whitespace")
        else if (!/[0-9]/.test(value)) setError("your password doesn't have any digit")
        else if (/^[a-z-\d]*$/.test(value)) setError("your password doesn't have contain any capital letter")
        else setError("")
        break
      
      case "name":
        if (number == 0) {
          setError("   ")
          setErrorStatus(true)
        }
        else if (/[0-9]/.test(value)) { 
          setError("     ")
          setErrorStatus(true)
        }
        else if (number < 2) {
          setError("   ")
          setErrorStatus(true)
        }
        else if (number > 10) {
          setError("   ")
          setErrorStatus(true)
        }
        else setErrorStatus(false)
        break
      
      case "phone":
        if (number === 0) {
          setError("    ")
          setErrorStatus(true)
        }
        else if (number < 11) {
          setError("  ")
          setErrorStatus(true)
        }
        else if (number > 11) {
          setError("  ")
          setErrorStatus(true)
        }
        else if (value[0] != "8" || value[1] != "9") {
          setError("   ")
          setErrorStatus(true)
        }
        else {
          setErrorStatus(false)
        }
          
        break
      
      case "adress": 
        if (!/[-]/i.test(value)) {
          setError("     ")
          setErrorStatus(true)
        }
        else { setErrorStatus(false) }
        break
        
    }
  }, [value])

  return {error, errorStatus}
}