import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  statePopUpMessage: false,

  pizza: null,
  displayOne: false,
  displayTwo: false,


  infoMessage: {pervCall: false, currentCall: false}, /// pop up message
  visible: false,

  checkValidation: false,

}

const popUpSlice = createSlice({
  name: "popUp",
  initialState,
  reducers: {
    changePopUpToTrue(state) {
      state.statePopUpMessage = true
    },
    changePopUpToFalse(state ) {
      state.statePopUpMessage = false
    },
    getNewPizza(state, action) {
      state.pizza = action.payload
    },
    showOwnMessage(state, action) {
      // console.log(action.payload)
      state.visible = true
      switch (action.payload) {
        case 0: state.displayOne = true
          break
        case 1: state.displayTwo = true
          break
      }
    },
    hideMessage(state) {
      state.displayOne = false
      state.displayTwo = false
      state.visible = false
    },
    hideInfoMessage(state) {
      state.infoMessage = {pervCall: true, currentCall: false}

    },
    showInfoMessage(state) {
      if (!state.infoMessage.pervCall) {
        state.infoMessage = {pervCall: true, currentCall: true}
      }
    },
    trueCheckValidation(state) {
      state.checkValidation = true
    },
    falseCheckValidation(state) {
      state.checkValidation = false
    },
    refreshPopUp(state) {
      state = {
      statePopUpMessage: false,
      pizza: null,
      displayOne: false,
      displayTwo: false,
      infoMessage: {pervCall: false, currentCall: false}, /// pop up message
      visible: false,
      checkValidation: false,
    }
    },

  }
});

export default popUpSlice.reducer;
export const selectPopUp = (state) => state.popUp
export const {
  changePopUpToTrue,
  changePopUpToFalse,
  getNewPizza,
  showOwnMessage,
  hideMessage,
  hideInfoMessage,
  showInfoMessage,
  trueCheckValidation,
  falseCheckValidation,
  refreshPopUp
} = popUpSlice.actions;

