import React from 'react';
import CartHederElement from './CartHederElement';
import LogoHeaderElement from './LogoHeaderElement';
import { Link, useLocation } from "react-router-dom";

function Header() {
  const location = useLocation().pathname 

    return (
      <header className="header wall-paper">
        <div className="container">
          <div className="header__body">
            <Link to="/">
              <LogoHeaderElement /> 
            </Link>
            {"/" === location && //it shows or hides <CartHederElement/> component depending on the path indicator 
            <Link to="/cart">
              <CartHederElement />
            </Link>
            }
            <Link to="/total" style={{border: "1px solid black", padding:"10px", marginLeft:"20px"}}> 
              total
            </Link>
          </div>
        </div>
      </header>
    );
}
export default Header;