import React from 'react';
import LiComponents from "../../../common/LiComponents";

type PizzaOptionsProps = {
  types:number[];
  sizes: number[];
  activType: number;
  activSize: number;
  setActiveType: React.Dispatch<React.SetStateAction<number>>;
  setActiveSise: React.Dispatch<React.SetStateAction<number>>;
}

const PizzaOptions: React.FC<PizzaOptionsProps> = ({
  types,
  sizes,
  activType,
  activSize,
  setActiveType,
  setActiveSise,
}) => {
  // const showTitle = useSelector((state)=> state.popUp.statePopUpMessage)

  return (
    <>
      <ul className="options__type">
        <LiComponents
          li_s={types}
          text={null}
          callback={setActiveType}
          value={activType}
        />
      </ul>
      <ul className="options__size">
        <LiComponents
          li_s={sizes}
          text={""}
          callback={setActiveSise}
          value={activSize}
          
        />
      </ul>
    </>

  );
}
export default PizzaOptions;