import React from 'react';
import { ReactComponent as Error_loading } from '../assets/img/error_loading.svg'
import cl from "./styles/ErrorLoadPage.module.css"
import { useSelector } from 'react-redux';

function ErrorLoadPage() {
  const message = useSelector((state) => state.cart.error)
  return (
    <div className={cl.container}>
      <h3 className={cl.title}> </h3>
      <Error_loading className={cl.logo} />
      <p className={cl.message}> {message}</p>
    </div>
  );
}
export default ErrorLoadPage;