import React from 'react';
import { useSelector } from 'react-redux';
import { selectCart } from "../../../redux/slices/cartSlice";
import cl from "./style/FormOrder.module.css"
import { selectPopUp } from "../../../redux/slices/popUpSlice";
import Input from './Input';
import useInput from '../../business/customHooks/useInput';
import Button from './Button';


const FormOrder: React.FC = () => {
  const { price }: { price: number } = useSelector(selectCart)
  const { displayOne }: { displayOne: boolean } = useSelector(selectPopUp)

  const phone = useInput("phone")
  const name = useInput("name")
  const adress = useInput("adress")
  const email = useInput("email")

  return (
    <>
      {
        displayOne &&
        <div className={cl.container}>
          <div className={cl.price}>
            <img className={cl.img} src="https://cdn.dodostatic.net/static/Img/ComboTemplates/a529a1677db44979b432324671b48a8b_584x584.jpeg" width="100px" alt="" />
              <p className={cl.priceText}>   <span>{price}</span> </p>
          </div>

          <form className={cl.form} action="#">
              <Input
                type="number"
                placeholder=" "
                data={phone}
                name="phone"
              />
              <Input
                type="text"
                placeholder=" "
                data={name}
                name="phone"
              />
              <Input
                type="text"
                placeholder=" "
                data={adress}
                name="adress"
              />
              <Input
                type="text"
                placeholder=" email"
                data={email}
                name="email"
              />

              <Button
                list={
                  [
                    phone.errorStatus,
                    name.errorStatus,
                    adress.errorStatus,
                  ]
                }
              /> 
          </form>
        </div>
      }

    </>


  );
}
export default FormOrder;