import React, {useRef} from 'react';
import { useSelector } from 'react-redux';
import { selectCart } from "../../../redux/slices/cartSlice";
import cl from "./style/FormOrder.module.css"
import { selectPopUp } from "../../../redux/slices/popUpSlice";
import useInput from '../../business/customHooks/useInput';
import Input from './Input';
import Button from './Button';

const FormOrderOwn: React.FC = () => {
  const { price }: { price: number } = useSelector(selectCart)
  const { displayTwo }: { displayTwo: boolean } = useSelector(selectPopUp)
  


  const phone = useInput("phone")
  const name = useInput("name")
  return (
    <>
      {displayTwo &&
        <div className={cl.container} >
          <div className={cl.price}>
            <img className={cl.img} src="https://cdn.dodostatic.net/static/Img/ComboTemplates/a529a1677db44979b432324671b48a8b_584x584.jpeg" width="100px" alt="" />
            <p className={cl.priceText}>  <span>{price}</span> </p>
          </div>

          <form className={cl.form} >
            <Input
              type="number"
              placeholder=" "
              data={phone}
              name="phone"
            />
            <Input
              type="text"
              placeholder=" "
              data={name}
              name="name"
            />

            <Button
              list={[phone.errorStatus, name.errorStatus]}
            />
  
          </form>

        </div>
      }
    </>
    
    
  );
}
export default FormOrderOwn;