import { createSlice, createAsyncThunk,PayloadAction } from "@reduxjs/toolkit";
import axios from "axios";
import isEqual from "lodash.isequal";
import { RootState } from "../store";
import {InitialCart, CartPizzasCart} from "../../types/pizzasTipes"

export const fetchPizzas = createAsyncThunk(
  'cart/fetchPizzas',
  async (indexCategory, sortPizza) => {
    const response = await axios.get(`https://641d44e61a68dc9e461a633b.mockapi.io/items?category=${indexCategory}&sortby=${sortPizza}`)
    const { data } = response
    return data
  }
)


const initialState : InitialCart = {
  pizzasResponse: null,
  status: "",
  error: "",
  pizzas: [],
  countPizzas: 0,
  price: 0,
}

const cartSlice = createSlice({
  name: "cart",
  initialState,
  reducers: {
    addPizza(state, action: PayloadAction<CartPizzasCart>) {
      state.countPizzas += 1;
      state.price += action.payload.price;
      // todo =============================================================
      let add = false;
      const { count, ...clone } = action.payload
      for (let i = 0; i < state.pizzas.length; i++) {
        const { count, ...cloneTwo } = state.pizzas[i]
        if (isEqual(clone, cloneTwo)) {
          state.pizzas[i].count += 1;
          add = true;
          break;
        }
      }
      if (!add) {
        state.pizzas.push(action.payload);
      }
      // todo =============================================================
    },
    addOne(state, action: PayloadAction<CartPizzasCart>) {
      state.countPizzas += 1;
      state.price += action.payload.price;
      for (let i = 0; i < state.pizzas.length; i++) {
        if (isEqual(state.pizzas[i], action.payload)) {
          state.pizzas[i].count += 1
          break
        }
      }
    },
    removeOne(state, action) {

      for (let i = 0; i < state.pizzas.length; i++) {
        if (isEqual(state.pizzas[i], action.payload)) {
          state.pizzas[i].count -= 1;
          state.countPizzas -= 1;
          state.price -= action.payload.price;
          if (state.pizzas[i].count < 1) {
            state.pizzas = state.pizzas.filter((item, index) =>
              index !== i
            )
          }
          break;
        }
      }
    },
    cleanRow(state, action) {
      for (let i = 0; i < state.pizzas.length; i++) {
        if (isEqual(state.pizzas[i], action.payload)) {
          state.countPizzas -= state.pizzas[i].count;
          state.price -= state.pizzas[i].count * state.pizzas[i].price
          state.pizzas = state.pizzas.filter((item, index) => index !== i);
          break
        }
      }
    },
    cleanAll(state) {
      state.pizzas = []
      state.price = 0
      state.countPizzas = 0
    },
    refreshCart(state) {
      state = {
      pizzasResponse: null,
      status: "",
      error: "",
      pizzas: [],
      countPizzas: 0,
      price: 0,
      }
    },

  },
  extraReducers: (builder) => {
    builder.addCase(fetchPizzas.fulfilled, (state, action) => {
      state.error = ""
      state.pizzasResponse = action.payload
      state.status = "success"
    })
    builder.addCase(fetchPizzas.pending, (state) => {
      state.status = "prosessing"
    })
    builder.addCase(fetchPizzas.rejected, (state, action) => {
      state.status = "error"
      state.error = (action.error.message) ? action.error.message : ""
      state.pizzasResponse = null
    })
  }
});


export default cartSlice.reducer;
export const selectPizzas = (state: RootState) => state.cart.pizzasResponse
export const selectCart = (state: RootState) => state.cart
export const {
  addPizza,
  cleanAll,
  addOne,
  removeOne,
  cleanRow,
  refreshCart
} = cartSlice.actions;
